* {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  width: 100%;
  display: block;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-image: url("https://pixabay.com/get/g8734f818454e0fba9f3d8891e29982f5dd22e937e957e029c211ffb5e349a9ffbe2cb1291a74baa3de4fd87a3bb4af73.png"), linear-gradient(180.11deg, #e5e2e3 10.73%, #fdfefe 38.02%, #e5e2e3 71.11%, #fdfefe 99.91%);
  background-repeat: no-repeat;
  background-size: cover;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

ul {
  justify-content: center;
  gap: 20px;
  padding: 0;
  list-style: none;
  display: flex;
}

.btn {
  letter-spacing: .06em;
  color: #fff;
  cursor: pointer;
  background: linear-gradient(to right, #bc3220 0%, #db4a37 29%, #db4a37 50%, #db4a37 71%, #bc3220 100%);
  border: none;
  border-radius: 4px;
  padding: 6px 22px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.87;
  text-decoration: none;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.btn:hover, .btn:focus {
  color: #353535;
  text-shadow: -1px -1px #b86200, 0 1px #ecb76b;
  background: linear-gradient(#ffbf00 0%, #fa9f17 100%);
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.heartbeat {
  animation: heartbeat 1.5s ease-in-out infinite both;
}

@keyframes heartbeat {
  from {
    transform-origin: center;
    animation-timing-function: ease-out;
    transform: scale(1);
  }

  10% {
    animation-timing-function: ease-in;
    transform: scale(.91);
  }

  17% {
    animation-timing-function: ease-out;
    transform: scale(.98);
  }

  33% {
    animation-timing-function: ease-in;
    transform: scale(.87);
  }

  45% {
    animation-timing-function: ease-out;
    transform: scale(1);
  }
}

.slide-in-elliptic-top-fwd {
  animation: slide-in-elliptic-top-fwd .7s cubic-bezier(.25, .46, .45, .94) both;
}

@keyframes slide-in-elliptic-top-fwd {
  0% {
    transform-origin: 50% 100%;
    opacity: 0;
    transform: translateY(-600px)rotateX(-30deg)scale(0);
  }

  100% {
    -webkit-transform: translateY(0) rotateX(0) scale(1);
    transform: translateY(0) rotateX(0) scale(1);
    transform-origin: 50% 1400px;
    opacity: 1;
  }
}

.bounce-in-bottom {
  animation: bounce-in-bottom 1.1s both;
}

@keyframes bounce-in-bottom {
  0% {
    opacity: 0;
    animation-timing-function: ease-in;
    transform: translateY(500px);
  }

  38% {
    opacity: 1;
    animation-timing-function: ease-out;
    transform: translateY(0);
  }

  55% {
    animation-timing-function: ease-in;
    transform: translateY(65px);
  }

  72% {
    animation-timing-function: ease-out;
    transform: translateY(0);
  }

  81% {
    animation-timing-function: ease-in;
    transform: translateY(28px);
  }

  90% {
    animation-timing-function: ease-out;
    transform: translateY(0);
  }

  95% {
    animation-timing-function: ease-in;
    transform: translateY(8px);
  }

  100% {
    animation-timing-function: ease-out;
    transform: translateY(0);
  }
}

